.button {
  padding: 4px 16px;
  font-size: 18px;
  font-weight: 600;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  // background-color: red;
}

.button[disabled] {
  background: repeating-linear-gradient(
    45deg,
    #dbdbdb,
    #dbdbdb 5px,
    #999999 2px,
    #999999 10px
  );
  color: black;
  cursor: not-allowed;
}
